import * as React from "react"
import { Link } from "gatsby"

// styles
const pageStyles = {
  color: "#232129",
  padding: "96px",
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
};

// markup
const AnotherPage = () => {
  return (
    <main style={pageStyles}>
      <title>Another Page</title>
    <Link to="/">Home Page</Link>
      
    </main>
  );
};

export default AnotherPage;
